.contain {
	position: absolute;
	width: 100vw;
	height: 100%;
	padding: 0;
	margin: 0;
	background-repeat: no-repeat;
	background-image: url("./assets/bac.png");
	background-color: #f2f2f2;
	z-index: -1;
}
